<template>
  <div class="user" v-if="userInfo">
    <BasePanel>
      <template #panel-head>
        <router-link to="/">主页</router-link>
      </template>
      <template #panel-content>
        <div class="top">
          <div class="user-top">
            <img :src="userInfo.avatar_url" alt="" />
            <!--头像-->
            <span>用户:{{ userInfo.loginname }}</span
            ><!-- 名称 -->
          </div>
          <div class="user-bottm">
            <p>{{ userInfo.score }}积分</p>
            <span class="iconfont icon-github">
              <router-link to="/"><span>@{{ userInfo.githubUsername }}</span></router-link>
            </span>
            <p class="sj">{{ userInfo.create_at | fromNow }}</p>
          </div>
        </div>
      </template>
    </BasePanel>
    <BasePanel>
      <template #panel-head> 最近创建的话题 </template>
      <template #panel-content>
        <TopicItem
          :isUser="true"
          v-for="item in userInfo.recent_topics"
          :key="item.id"
          v-bind="item"
        />
      </template>
    </BasePanel>
    <BasePanel>
      <template #panel-head> 最近参与的话题 </template>
      <template #panel-content>
        <TopicItem
          :isUser="true"
          v-for="item in userInfo.recent_replies"
          :key="item.id"
          v-bind="item"
        /> </template
    ></BasePanel>
  </div>
</template>

<script>
import BasePanel from "../components/layout/BasePanel.vue";
import TopicItem from "../components/TopicItem.vue";
import { getUserInfo } from "../plugins/api";
export default {
  components: { BasePanel, TopicItem },
  props: ["loginname"],
  data() {
    return {
      userInfo: [],
    };
  },
  watch: {
    loginname: {
      async handler(val) {
        const res = await getUserInfo(val);
        console.log(res);
        this.userInfo = res.data;
      },
      immediate: true,
    },
  },
};
</script>

<style lang='less'>
.user {
  .top {
    padding-top: 13px;
    margin-left: 10px;
    .user-top {
      img {
        width: 60px;
      }
      span {
        position: relative;
        top: -41px;
        left: 13px;
        color: rgb(86, 87, 87);
        font-size: 14px;
      }
    }
    .user-bottm {
      p {
        padding: 7px 0 5px;
        color: rgb(83, 83, 83);
      }
      .sj {
        padding: 7px 0 20px;
      }
    }
  }
}
</style>